		url("../fonts/RoundedMplus1c-Black.woff2") format("woff2"),
		url("../fonts/RoundedMplus1c-Black.woff2") format("woff2");
}

@font-face {
	font-weight: 200;
	font-family: "RoundedMp";
	font-style: normal;
	font-display: swap;
	src:
		url("../fonts/RoundedMplus1c-Thin.woff2") format("woff2"),
		url("../fonts/RoundedMplus1c-Thin.woff2") format("woff2");
}
